import { Component, OnInit, OnDestroy } from '@angular/core';
import { SignupBasePage } from '../signup-base.page';
import { Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { UtilsService } from '../../@core/utils.service';
import { StorageService } from '../../@core/storage.service';
import { NavigationExtrasService } from '../../@core/navigation-extras.service';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { Location } from '@angular/common';
import { Constants } from '../../@global/constants';
import { FormCustomValidators } from '../../@shared/validators/form-custom-validators';
import { GoogleAnalyticsService } from '../../@core/google-analytics.service';
import { Camera } from '@ionic-native/camera/ngx';
import { InformService } from '../../@core/inform.service';

const PAGE_POSITION = 2;

@Component({
  selector: 'app-profile-name',
  templateUrl: './profile-name.page.html',
  styleUrls: ['./profile-name.page.scss'],
})
export class ProfileNamePage extends SignupBasePage implements OnInit, OnDestroy {
  public formGroup: FormGroup;
  public maxlengthName = Constants.MAX_LENGTH_NAME_COGNITO;

  constructor(
    router: Router,
    location: Location,
    store: Store,
    utilsService: UtilsService,
    camera: Camera,
    informService: InformService,
    storage: StorageService,
    navigationExtrasService: NavigationExtrasService,
    googleAnalyticsService: GoogleAnalyticsService,
    private formBuilder: FormBuilder
  ) {
    super(
      location,
      router,
      store,
      utilsService,
      camera,
      informService,
      storage,
      navigationExtrasService,
      googleAnalyticsService
    );
  }

  ngOnInit() {
    this.initForm();
  }

  doIonViewWillEnter() {
    // override showBackButton
    this.showBackButton = this.checkGoBack();
  }

  private initForm(): void {
    this.formGroup = this.formBuilder.group({
      name: [
        '',
        [
          Validators.required,
          Validators.maxLength(this.maxlengthName),
          FormCustomValidators.notAllowedEmojis,
          FormCustomValidators.notAllowedNumbers,
        ],
      ],
      surname: [
        '',
        [
          Validators.required,
          Validators.maxLength(this.maxlengthName),
          FormCustomValidators.notAllowedEmojis,
          FormCustomValidators.notAllowedNumbers,
        ],
      ],
    });
  }

  public async saveProfileName() {
    if (this.utilsService.isValidForm(this.formGroup)) {
      await this.updateSignupFlowInfo(PAGE_POSITION, 'name', this.formGroup.get('name').value);
      await this.updateSignupFlowInfo(PAGE_POSITION, 'surname', this.formGroup.get('surname').value);
      this.goNextFlowPage(PAGE_POSITION);
    }
  }

  ngOnDestroy() {}
}
